export type MessagesType = { [key: string]: ToastType }

export function getMessages(lang: string): MessagesType {
  const messages = _.mapValues(errorMap, (o) => {
    return {
      level: o.level,
      message: lang === 'zh-CN' ? o.messageCn : o.message,
    } as ToastType
  })
  return messages
}
